import {getStrapiMedia} from "../../utils/medias";
import main from '../../assets/js/main.js'
import {md_render} from "../../helpers/functions";

export default {
  name: "blogSlug",
  async asyncData({$strapi,params}){

    return {
      article:await $strapi.find('articles',{slug:params.slug})
    }
  },
  methods:{
    getStrapiMedia,
    md_render
  },
  mounted() {
    main()
  }
}
